.services
{
    padding: 0 3rem 0 3rem;
    margin-top: 9rem;
    margin-bottom: 19rem;
    display: flex;
    height: 90vh;
}
.aw_txt
{
    display: flex;
    flex-direction: column;
    position: relative;
}
.aw_txt>:nth-child(1)
{
    color: var(--black);
    font-size: 2.3rem;
    font-weight: bold;
}
.aw_txt>:nth-child(2)
{
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
}
.aw_txt>:nth-child(3)
{
    color: var(--gray);
    font-size: 14px;
    margin-top: 1rem;
}
.s-button
{
    width: 8rem;
    height: 2rem;
    margin-top: 1rem;
}
.cards
{
    position: relative;
}
.cards>*
{
    position: absolute;
}
.s-blur1
{
    top: 13rem;
    left: -18rem;
}
.s-blur2
{
    top: 8rem;
    left: 14rem;
}

@media screen and (max-width: 480px) 
{
    .services
    {
        flex-direction: column;
        margin-top: 0;
        gap: 5rem;
        height: 66rem;
        padding: 0;
    }
    .cards
    {
        display: flex;
        flex-direction: column;
        gap: 17rem;
    }
    .cards>*
    {
        position: static;
    }
}