.footer
{
    display: flex;
    align-items: center;
    margin: -0.5rem -3.5rem;
    position: relative;
    /* margin-top: -5rem; */
}
.f-content
{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    margin-top: 4rem;
    gap: 4rem;
    color: white;
}
.f-icons
{
    display: flex;
    gap: 2rem;
}

@media screen and (max-width: 480px) 
{
    
    .f-content
    { 
        gap: 2rem;
        transform: scale(0.8);
    }
    .f-icons
    {
        transform: scale(0.7);
    }
}