:root {
  --yellow: #f5c32c;
  --orange: #fca61f;
  --black: #242d49;
  --gray: #788097;
  --blueCard: #ddf8fe;
  --purple: rgb(238 210 255);
  --dark: #121212;
  --boxShadow: 0px 19px 60px rgb(0 0 0 / 8%);
  --orangeCard: rgba(252, 166, 31, 0.45);
  --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

.App {
  padding: 0.5rem 3.5rem;
  overflow: hidden;
  color: var(--black);
}

.button {
  border-radius: 34px;
  border: none;
  font-size: 16px;
  color: white;
  padding: 11px 28px 11px 28px;
  background: rgb(255, 196, 8);
  background: linear-gradient(
    238deg,
    rgba(255, 196, 8, 1) 0%,
    rgba(253, 152, 47, 1) 59%
  );
  box-shadow: 0 20px 24px 3px rgb(251, 161, 40, 0.42);
}
.button:hover
{
   background: white;
   color: var(--orange);
   border: 1px solid var(--orange);
   cursor: pointer;
}


@media screen and (max-width: 480px) {
  .App{
  padding: 0.5rem 1rem;
  }
}